import amap from '@/static/js/amap-wx.js';
export default {
	data() {
		return {
			token: '',
			marketid: '',
			swiperList: [],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			hotList: [], //热卖单品
			towHolist: [], //热卖单品两个
			goodList: [], //优选商品
			mydata: {

			}, //请求参数
			belongList: [], //专属菜品
			myAmap: ''
		}
	},
	onReady() {
		this.getLocationInfo()

	},
	onLoad(options) {
		this.myAmap = new amap.AMapWX({
			key: 'fc7b2b1171e49890b952144ed8562195'
		})
		this.getAddress()

	},
	onShow() {
		this.mydata.marketid = '1254';
		this.token = uni.getStorageSync('token')
		uni.setStorage({
			key: 'marketid',
			data: '1254',
		})
		this.getSwiper()
		this.getHot();
		this.getbelongPord()
		this.getGoodList()
		this.getShopCarList()
	},
	onShareAppMessage:function(res){
		return {
			path: '/pages/index/index',
		}
	
	},
	methods: {
		//获取地理位置
		getLocationInfo: function() { //1. uniapp弹窗弹出获取授权（地理，个人微信信息等授权信息）弹窗
			uni.getLocation({
				type: 'wgs84',
				success(res) {
					// console.log(res)
					getApp().globalData.longitude = res.longitude
					getApp().globalData.latitude = res.latitude
				},
				fail() { //1.2 拒绝授权
					console.log("你拒绝了授权，无法获得周边信息")
				}
			})
		},

		//获得当前地址
		getAddress: function() {
			this.myAmap.getRegeo({
				location: this.location,
				success: (data) => {
					// console.log(data)
					getApp().globalData.now_city = data[0].regeocodeData.addressComponent.city
				},
				fail: function(info) {
					//失败回调
					//  console.log(info)
				}
			})
		},

		//获取轮播
		getSwiper: function() {
			var that = this;
			this.$urlRequest('getIndexImg.do', this.mydata).then(res => {
				for (var item of res.data) {
					for (var key in item) {
						if (key == 'img') {
							item[key] = JSON.parse(item[key])
						}
					}
				}
				this.swiperList = res.data
			})
		},
		//获取热卖单品
		getHot: function() {
			this.$urlRequest('getIndexPageProductOrderByKC.do', this.mydata).then(res => {
				for (var item of res.data) {
					for (var key in item) {
						if (item[key] == null || item[key] == 'null') {
							item[key] = ''
						}
						if (key == 'prodImg') {
							item[key] = JSON.parse(item[key])
						}
					}
				}
				this.hotList = res.data
				this.towHolist = res.data.slice(0, 2)
			})
		},

		//优选
		getGoodList: function() {
			this.$urlRequest('getIndexPageProduct.do', this.mydata).then(res => {
				for (var item of res.data) {
					for (var key in item) {
						if (item[key] == null || item[key] == 'null') {
							item[key] = ''
						}
						if (key == 'prodImg') {
							item[key] = JSON.parse(item[key])
						}
					}
				}
				this.goodList = res.data.slice(0, 2)
			})
		},
		//专属菜品
		getbelongPord: function() {
			var that = this;
			this.$urlRequest('getIndexPageProductOrderByTime.do', this.mydata).then(res => {
				for (var item of res.data) {
					for (var key in item) {
						if (item[key] == null || item[key] == 'null') {
							item[key] = ''
						}
						if (key == 'prodImg') {
							item[key] = JSON.parse(item[key])
						}
					}
				}
				this.belongList = res.data
			})
		},
		//购物车添加事件
		add_shoppingcar: function(prodId) {
			if (!!!this.token) {
				uni.getSetting({
					success(res) {
						// console.log(res)
						uni.navigateTo({
							url: '/pages/login/login_type',
						})
						// if (res.authSetting['scope.userInfo']) {
						// 	// 已经授权，可以直接调用 getUserInfo 获取头像昵称
						// 	uni.navigateTo({
						// 		url: '/pages/login/login_type',
						// 	})
						// } else {
						// 	uni.navigateTo({
						// 		url: '/pages/getUserInfo/getUserInfo',
						// 	})
						// }
					}
				})
			} else {
				var data = {
					token: this.token,
					prodid: prodId
				}
				uni.showLoading({
					title:'正在加入购物车'
				})
				this.$urlRequest('addBusinessCarProd.do', data).then(res => {
					uni.hideLoading()
					if (res.state == 'true') {
						this.$myToast('已加入购物车')
						this.getShopCarList()
					}else{
						this.$myToast(res.state)
					}
				})
			}

		},
		//前往商品详情
		gotoStore: function(prodId) {
			uni.navigateTo({
				url: '/pages/prodDetail/prodDetail?prodId=' + prodId
			})
		},
		//前往搜索
		gotoSearch: function() {
			uni.navigateTo({
				url: '/pages/search/search'
			})
		},
		//获取购物车
		getShopCarList:function(){
			if(uni.getStorageSync('token') != ''){
				this.$urlRequest('getCar.do', {
					token: uni.getStorageSync('token')
				}).then(res => {
					if(res.length != 0){
						var prodNumber = 0
						for (var item of res[0].prodList) {					
							prodNumber += parseInt(item.prodCount)
						}
						uni.setTabBarBadge({
							index: 2,
							text: "" + prodNumber + ""
						})
					}	
				})
			}
		}

	}
}
